.contact {
    height: 100vh;
    position: relative;
  }
  
  .contact-bg {
    width: 20px;
    height: 100%;
    background-color: #ccc;
    position: absolute;
  }
  
  .contact-wrapper {
    margin-top: 5%;
    padding: 50px;
    display: flex;
  }
  
  .contact-left {
    flex: 1;
  }
  
  .contact-right {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .contact-title {
    text-align: center;
    font-size: 45px;
    width: 80%;
  }
  
  .c-info-item {
    display: flex;
    align-items: center;
    margin: 50px 0px;
    font-weight: 300;
    width: 70%;
  }
  
  .c-icon {
    width: 30px;
    height: 30px;
    margin-right: 20px;
  }
  
  .c-desc {
    font-weight: 200;
  }
  
  form {
    margin-top: 20px;
  }
  
  input {
    width: 50%;
    height: 50px;
    border: none;
    border-bottom: 1px solid black;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
  }
  
  textarea {
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
  }
  
  button {
    border: none;
    padding: 15px;
    background-color: #313131;
    color: white;
    font-weight: 500;
    cursor: pointer;
  }
  
  .c-announcement{
    margin-top: 10px;
  }
  @media screen and (max-width: 480px) {
    .contact-wrapper {
      flex-direction: column;
      padding: 0px 50px;
    }
  
    .contact-title {
      font-size: 30px;
    }
  
    .c-info-item {
      margin: 20px 0px;
      width: 100%;
    }
  
    /* .c-desc {
      display: none;
    } */
  
    form {
      margin-top: 0px;
      display: flex;
      flex-wrap: wrap;
    }
  
    input {
      width: 50%;
      height: 50px;
      margin: 10px;
      margin-left: 0;
    }
  
    button {
      margin-top: 10px;
    }
  }